<template>
  <v-container>
    <div style="margin-top: 150px;">
      <h2 class="display-1 mb-5">
        Blogg
      </h2>

      <v-layout justify-end mb-5>
        <div style="max-width: 400px; display: flex; align-items: center;">
          <v-text-field
            label="Søk i blogg"
            append-icon="search"
            v-model="searchTerm"
            @keyup.enter="searchPosts"
          ></v-text-field>

          <v-btn depressed @click="searchPosts">
            søk
          </v-btn>
        </div>
      </v-layout>

      <v-layout justify-center class="categories" mb-3>
        <div v-for="(category, i) in categories" :key="i" class="category">
          {{ category.name }}
        </div>
      </v-layout>
      <!-- <h3>Nyeste blogginnlegg</h3> -->

      <v-layout wrap justify-center>
        <v-flex xs12 sm12 md6 lg4 xl4 mb-5 v-for="(post, i) in posts" :key="i">
          <v-card class="program-card ml-3 mr-3 mt-3">
            <div class="fake-image">
              Bilde her {{ post.featured_media }}
            </div>

            <div class="title-wrapper">
              <v-card-title primary-title class="pl-4 pr-4">
                <div>
                  <h3 class="headline" v-html="post.title.rendered"></h3>
                </div>
              </v-card-title>

              <v-card-text>
                <div v-html="post.excerpt.rendered"></div>
              </v-card-text>
            </div>

            <v-card-actions class="justify-center">
              <div style="margin-bottom: 6px;">
                <router-link class="button submit" tag="button" :to="'/innlegg/' + post.id">
                  <span>Les innlegg</span>
                </router-link>
              </div>
            </v-card-actions>
          </v-card>
        </v-flex>
      </v-layout>
    </div>
  </v-container>
</template>

<script>
export default {
  data () {
    return {
      posts: [],
      searchTerm: '',
      categories: []
    }
  },

  created () {
    this.fetchPosts()
    this.fetchCategories()
  },

  methods: {
    fetchPosts () {
      this.$axios.get('/wordpress/wp-json/wp/v2/posts').then((items) => {
        console.log(items.data)
        this.posts = items.data
      })
    },

    fetchCategories () {
      this.$axios.get('/wordpress/wp-json/wp/v2/categories').then((items) => {
        console.log(items.data)
        this.categories = items.data
      })
    },

    searchPosts () {
      this.$axios.get('/wordpress/wp-json/wp/v2/posts?search=' + this.searchTerm).then((items) => {
        console.log(items.data)
      })
    }
  }
}
</script>

<style scoped lang="css">
    .fake-image {
    width: 100%;
    height: 200px;
    margin: 0;
    padding: 10px;
    background-color: #ddd;
  }

  .program-card {
    height: 100%;
    display: flex;
    flex-direction: column;
  }

  .title-wrapper {
    flex-grow: 1;
  }

  .categories {
    display: flex;
    flex-wrap: wrap;
  }

  .category {
    background: #8ACB88;
    color: #fff;
    padding: 10px 15px;
    margin: 5px;
    cursor: pointer;
    /*font-weight: 200;*/
  }
</style>
